// Typographer

/* fonts */
@font-face {
    font-family: 'StanleyPoster';
    src: url('/assets/fonts/StanleyPoster-Regular.eot'); /* IE9 Compat Modes */
    src: url('/assets/fonts/StanleyPoster-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url(data:application/font-woff;charset=utf-8;base64,) format('woff'), /* Modern Browsers */
         url('/assets/fonts/StanleyPoster-Regular.svg#StanleyPoster') format('svg'); /* Legacy iOS */
    font-style: normal;
    font-weight: normal;
    text-rendering: optimizeLegibility;
}

$font-display: "StanleyPoster", serif;
$font-body: "Karla", Helvetica, Arial, sans-serif;

%small-caps {
  letter-spacing: .25em;
  text-transform: uppercase;
  font: 12px/1.833333333em $font-body;
}

%title {
  font: 24px/1.375em $font-display;
}

%smooth {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
